'use client';
import { useState } from 'react';

export default function DashboardPage() {
  const [activeTab, setActiveTab] = useState('submitted');

  return (
    <div className="font-sans bg-gray-100 min-h-screen text-gray-900">
      <nav className="flex justify-center bg-[#0057A0] space-x-4 py-2">
        <button
          onClick={() => setActiveTab('submitted')}
          className={`px-4 py-2 text-white rounded ${
            activeTab === 'submitted' ? 'bg-[#C8102E]' : 'hover:bg-[#C8102E]'
          }`}
        >
          Submitted Applications
        </button>
        <button
          onClick={() => setActiveTab('approved')}
          className={`px-4 py-2 text-white rounded ${
            activeTab === 'approved' ? 'bg-[#C8102E]' : 'hover:bg-[#C8102E]'
          }`}
        >
          Approved Applications
        </button>
        <button
          onClick={() => setActiveTab('queried')}
          className={`px-4 py-2 text-white rounded ${
            activeTab === 'queried' ? 'bg-[#C8102E]' : 'hover:bg-[#C8102E]'
          }`}
        >
          Queried Applications
        </button>
        <button
          onClick={() => setActiveTab('reports')}
          className={`px-4 py-2 text-white rounded ${
            activeTab === 'reports' ? 'bg-[#C8102E]' : 'hover:bg-[#C8102E]'
          }`}
        >
          Reports
        </button>
        <button
          onClick={() => setActiveTab('records')}
          className={`px-4 py-2 text-white rounded ${
            activeTab === 'records' ? 'bg-[#C8102E]' : 'hover:bg-[#C8102E]'
          }`}
        >
          Account Records
        </button>
      </nav>

      <main className="max-w-4xl mx-auto my-10 bg-white p-8 rounded-lg shadow">
        {activeTab === 'submitted' && (
          <section>
            <h2 className="text-xl font-bold mb-4">Submitted Applications</h2>
            <div className="bg-gray-100 p-4 border rounded mb-4">
              <p>10 new applications received today.</p>
              <p>Click any application to view client biodata and uploaded documents.</p>
            </div>
          </section>
        )}

        {activeTab === 'approved' && (
          <section>
            <h2 className="text-xl font-bold mb-4">Approved Applications</h2>
            <div className="bg-gray-100 p-4 border rounded mb-4">
              <p>5 applications have been approved this week.</p>
              <p>Offers have been auto-generated based on RSA balances.</p>
            </div>
          </section>
        )}

        {activeTab === 'queried' && (
          <section>
            <h2 className="text-xl font-bold mb-4">Queried Applications</h2>
            <div className="bg-gray-100 p-4 border rounded mb-4">
              <p>2 applications require client document updates or clarifications.</p>
              <p>Pending client feedback before resubmission.</p>
            </div>
          </section>
        )}

        {activeTab === 'reports' && (
          <section>
            <h2 className="text-xl font-bold mb-4">Reports</h2>
            <div className="bg-gray-100 p-4 border rounded mb-4">
              <p>Summary of applications by PFA, submission date, and approval status.</p>
              <p>Download CSV or Excel formats for official reporting.</p>
            </div>
          </section>
        )}

        {activeTab === 'records' && (
          <section>
            <h2 className="text-xl font-bold mb-4">Account Records</h2>
            <div className="bg-gray-100 p-4 border rounded mb-4">
              <p>Showing account records with unique account numbers and addresses.</p>
              <p>Use search or upload Excel to update records in bulk.</p>
            </div>
          </section>
        )}
      </main>
    </div>
  );
}
